          </div>
          <div>
            <div class="s_img2">
              <img src="../../assets/enter_img/客服1.png" alt="" />
            </div>
            <b>{{ string.maxnum }}</b>
            <span>最大坐席数</span>
          </div>
          <div>
            <div class="s_img3">
              <img src="../../assets/enter_img/客服.png" alt="" />
            </div>
            <b>{{ string.num }}</b>
            <span>实际坐席数</span>
          </div>
          <div>
            <div class="s_img4">
              <img src="../../assets/enter_img/客服中心.png" alt="" />
            </div>
            <b>{{ string.stopnum }}</b>
            <span>停用坐席数</span>
          </div>
          <div>
            <div class="s_img5">
              <img src="../../assets/enter_img/线路.png" alt="" />
            </div>
            <b>{{ string.maxconcurrency }}</b>
            <span>最大登录并发总数</span>
          </div>
          <div>
            <div class="s_img6">
              <img src="../../assets/enter_img/电话.png" alt="" />
            </div>
            <b>{{ string.yucenum }}</b>
            <span>预测式外呼坐席并发总数</span>
          </div>
        </div>
        <div class="information">
          <p>
            超级企业信息
            <a-button type="link"><a-icon type="highlight" /></a-button>
            <span>
              当前为{{ statussty }}状态
              <a href="" @click.prevent="stu">{{ this.statussty1 }}企业</a>
            </span>
          </p>
          <div>
            <p>
              <span>超级企业名称</span><b>&emsp;{{ string.ename }}</b>
            </p>
            <p>
              <span>企业域名</span><b>&emsp;{{ string.ipstr }}</b>
            </p>
            <p>
              <span>超级管理员账号</span><b>&emsp;{{ string.adminname }}</b>
            </p>
            <p>
              <span>邮箱</span><b>&emsp;{{ string.adminemil }}</b>
            </p>
          </div>
          <div>
            <p>
              <span>超级企业ID</span><b>&emsp;{{ string.id }}</b>
            </p>
            <p>
              <span>最大坐席数</span><b>&emsp;{{ string.maxnum }}</b>
            </p>
            <p>
              <span>最大登录并发总数</span
              ><b>&emsp;{{ string.maxconcurrency }}</b>
            </p>
            <p>
              <span>创建时间</span><b>&emsp;{{ formattedCreateTime }}</b>
            </p>
          </div>
        </div>
      </a-tab-pane>
      <a-tab-pane key="2" tab="子企业管理">
        <div class="son_tab">
          <div class="s_top">
            <a-dropdown>
              <a-menu slot="overlay">
                <a-menu-item key="1">
                  <a-icon type="user" />1st menu item
                </a-menu-item>
                <a-menu-item key="2">
                  <a-icon type="user" />2nd menu item
                </a-menu-item>
                <a-menu-item key="3">
                  <a-icon type="user" />3rd item
                </a-menu-item>
              </a-menu>
              <a-button style="margin-left: 8px">
                客户测 <a-icon type="down" />
              </a-button>
            </a-dropdown>
            <span>
              当前为{{ this.statussty }}状态
              <a>{{ this.statussty1 }}企业</a>
            </span>
          </div>
          <div class="s_qiandan">
            <ul>
              <li>
                <h1>签单</h1>
                <p>企业类型</p>
              </li>
              <li>
                <h1>{{ string.maxnum }}</h1>
                <p>最大坐席数</p>
              </li>
              <li>
                <h1>{{ string.maxconcurrency }}</h1>
                <p>最大登录并发数</p>
              </li>
              <li>
                <h1>停用</h1>
                <p>API功能</p>
              </li>
              <li>
                <h1>启用</h1>
                <p>录音</p>
              </li>
            </ul>
          </div>
          <div class="s_bottom">
            <a-tabs>
              <a-tab-pane key="1" tab="信息">
                <div class="s_son1">
                  <p>
                    子企业基本信息
                    <a-button type="link"><a-icon type="highlight" /></a-button>
                  </p>
                  <div>
                    <p><span>子企业名称</span><b>&emsp;客户端</b></p>
                    <p><span>企业类型</span><b>&emsp;签单</b></p>
                    <p><span>截止日期</span><b>&emsp;--</b></p>
                  </div>
                  <div>
                    <p>
                      <span>最大坐席数</span><b>&emsp;{{ string.maxnum }}</b>
                    </p>
                    <p>
                      <span>最大登录并发数</span
                      ><b>&emsp;{{ string.maxconcurrency }}</b>
                    </p>
                    <p>
                      <span>创建时间</span
                      ><b>&emsp;{{ formattedCreateTime }}</b>
                    </p>
                  </div>
                </div>
              </a-tab-pane>
              <a-tab-pane key="2" tab="计费">
                <div class="s_son2">
                  <div>
                    <p>
                      子企业呼转配置
                      <a-button type="link"
                        ><a-icon type="highlight"
                      /></a-button>
                    </p>
                    <div>
                      <span>&emsp;呼转特殊配置&emsp;</span>
                      <a-switch default-checked />
                    </div>
                  </div>
                  <div>
                    <p>
                      子企业计费配置
                      <a-button type="link"
                        ><a-icon type="highlight"
                      /></a-button>
                    </p>
                    <div>
                      <span>&emsp;计费特殊配置&emsp;</span>
                      <a-switch default-checked />
                    </div>
                  </div>
                </div>
              </a-tab-pane>
            </a-tabs>
          </div>
        </div>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>
<script>
import { getStr, Status } from "../../api/EnterpriseManagement.js";
export default {
  data() {
    return {
      id: {},
      string: {},
      statussty: "",
      statussty1: "",
    };
  },
  created() {
    this.updateDataFromRoute(); // 初始化时也调用，确保首次加载时有数据
  },
  watch: {
    // 监听$route的变化，当路由参数变化时触发
    $route() {
      this.updateDataFromRoute();
    },
  },
  //将数组中的时间戳转换为时间形式
  computed: {
    formattedCreateTime() {
      const times = this.string.create_time;
      const date = new Date(times * 1000);
      return `${date.getFullYear()}-${("0" + (date.getMonth() + 1)).slice(
        -2
      )}-${("0" + date.getDate()).slice(-2)} ${("0" + date.getHours()).slice(
        -2
      )}:${("0" + date.getMinutes()).slice(-2)}:${(
        "0" + date.getSeconds()
      ).slice(-2)}`;
    },
  },
  methods: {
    stu() {
      if (this.string.status == 1) {
        Status(this.id, 0).then((res) => {
          console.log(res);
          this.updateDataFromRoute();
          this.$emit("1","2")
        });
      } else if (this.string.status == 0) {
        Status(this.id, 1).then((res) => {
          console.log(res);
          this.updateDataFromRoute();
          this.$emit("1","2")
        });
      }
    },
    statuss() {
      console.log(this.string);
      if (this.string.status == 1) {
        this.statussty = "启用";
        this.statussty1 = "停用";
      } else if (this.string.status == 0) {
        this.statussty = "停用";
        this.statussty1 = "启用";
      }
    },
    updateDataFromRoute() {
      this.id = this.$route.params.id;
      try {
        getStr(this.id).then((res) => {
          console.log(res);
          this.string = res.data;
          this.statuss();
        });
      } catch (error) {
        console.error("Error fetching detail:", error);
        // 可以在这里处理错误，比如显示错误消息给用户
      }
    },
  },
};
</script>
<style lang="less" scoped>
.super {
  border: 1px solid rgb(242, 242, 242);
  height: 200px;
  display: flex;
  justify-content: space-between;
  > div {
    width: 147px;
    > div:first-child {
      width: 80px;
      height: 80px;
      border: 1px solid rgb(245, 245, 245);
      border-radius: 50%;
      margin: auto;
      margin-top: 20px;
      > img {
        width: 40px;
        height: 40px;
        display: block;
        margin: auto;
        margin-top: 20px;
      }
    }
    > b {
      display: block;
      text-align: center;
      font-size: 20px;
      margin-top: 5px;
    }
    > span {
      display: block;
      color: rgb(190, 190, 190);
      text-align: center;
      margin-top: 10px;
    }

    .s_img1 {
      background: #5dacff;
    }
    .s_img2 {
      background: #ffab2d;
    }
    .s_img3 {
      background: #50dfc3;
    }
    .s_img4 {
      background: #ff9171;
    }
    .s_img5 {
      background: #83e8ac;
    }
    .s_img6 {
      background: #fed462;
    }
  }
}
.information {
  border: 1px solid rgb(242, 242, 242);
  margin-top: 30px;
  height: 300px;
  padding: 10px;
  position: relative;
  > p {
    span {
      float: right;
    }
  }
  > div {
    p {
      span {
        display: inline-block;
        width: 120px;
        text-align: right;
        line-height: 35px;
      }
    }
  }
  > div:nth-child(2) {
    position: absolute;
    top: 70px;
    left: 70px;
  }
  > div:last-child {
    position: absolute;
    top: 70px;
    left: 550px;
  }
}
.son_tab {
  .s_top {
    margin-bottom: 10px;
    span {
      float: right;
    }
  }
  .s_qiandan {
    height: 100px;
    border: 1px solid rgb(237, 237, 237);
    ul {
      height: 100%;
      display: flex;
      justify-content: space-between;
      padding: 0;
      li {
        list-style: none;
        flex-grow: 1;
        h1 {
          text-align: center;
          margin-top: 10px;
        }
        p {
          text-align: center;
          color: grey;
        }
      }
    }
  }
  .s_bottom {
    .s_son1 {
      height: 200px;
      border: 1px solid rgb(243, 243, 243);
      padding: 10px;
      position: relative;
      div {
        span {
          display: inline-block;
          width: 100px;
          text-align: right;
        }
      }
      > div:nth-child(2) {
        position: absolute;
        top: 70px;
        left: 70px;
      }
      > div:last-child {
        position: absolute;
        top: 70px;
        left: 550px;
      }
    }
    .s_son2 {
      > div {
        margin-bottom: 20px;
        height: 100px;
        border: 1px solid rgb(235, 235, 235);
        padding: 10px;
        span {
          color: rgb(172, 172, 172);
        }
      }
    }
  }
}
</style>
